<script setup>
import { getWetElectricity } from "@/api/wetElectricity";
import { ref, reactive, onMounted, toRaw } from "vue";

const formInline = reactive({
    time: ['', ''],
    timeSize: 'Min',
    time11: 'Min',
    data: '平均值',
    deviceName: "01e844f884844aa2bb5d1cab87316c17",
    deviceName11: "01e844f884844aa2bb5d1cab87316c17",
    total: "",
    // pickerOptions: disabledDate(time){
    //     return time.getTime() > Date.now()
    //     }
      
   
})
// const pickerOptions= {
//     disabledDate(time) {
//         return time.getTime() > Date.now()
//         }
//       }

//let定义后续使用加.value
let currPage1 = ref(1)
let pageSize1 = ref(10)

function search() {
    let payload = {
        currPage: currPage1.value,
        pageSize: pageSize1.value,
        dataTime: formInline.timeSize,//时间粒度
        boxId: formInline.deviceName,
        startTime: formInline.time[0],
        endTime: formInline.time[1],
    }
    getVocData(payload);
    formInline.time11 = formInline.timeSize
    formInline.deviceName11 = formInline.deviceName

}


let vocDate = reactive({
    vocDate1: [],
})

let dataGet = null
function getVocData(payload) {
    getWetElectricity(payload).then((res) => {
        vocDate.vocDate1 = []
        console.log(res);
        dataGet = res.data.data.dataSet
        // formInline.dataall=toRaw(res.data.data.dataSet[0].data)
        // formInline.dataall1=res.data.data.dataSet
        console.log(formInline.dataall, 123123123);
        formInline.total = res.data.data.totalCount
        if (formInline.timeSize != 'Min') {
            for (let x = 0; x < dataGet.length; x++) {
                let linshi = {}
                linshi["createTime"] = dataGet[x].createTime
                linshi["2ya"] = dataGet[x].data[formInline.data][0].value
                // linshi["2yak"] = dataGet[x].data[formInline.data][0].key
                // linshi["2yau"] = dataGet[x].data[formInline.data][0].unit
                linshi["2liu"] = dataGet[x].data[formInline.data][1].value
                linshi["1du"] = dataGet[x].data[formInline.data][2].value
                linshi["2du"] = dataGet[x].data[formInline.data][3].value
                linshi["3du"] = dataGet[x].data[formInline.data][4].value

                if (formInline.deviceName == "01e844f884844aa2bb5d1cab87316c17") {
                    linshi["4du"] = dataGet[x].data[formInline.data][5].value
                    linshi["name"] = "焙烧湿电"
                    console.log(linshi);
                } else if (formInline.deviceName == "5cba298477bc456ab1a2bd06e35cb0d8") {
                    linshi["name"] = "隧道窑湿电"
                    console.log(linshi);
                }
                else {
                    linshi["name"] = "石墨化湿电"
                    console.log(linshi);
                }
                vocDate.vocDate1.push(linshi)

            }
        } else {
            for (let x = 0; x < dataGet.length; x++) {
                let linshi = {}
                linshi["createTime"] = dataGet[x].createTime
                linshi["zidong"] = dataGet[x].data[0].value
                linshi["yunxing"] = dataGet[x].data[1].value
                linshi["guoya"] = dataGet[x].data[2].value
                linshi["shuibeituo"] = dataGet[x].data[3].value
                linshi["shuiyunxing"] = dataGet[x].data[4].value
                linshi["shuiguzhang"] = dataGet[x].data[5].value
                linshi["faquankai"] = dataGet[x].data[6].value
                linshi["faquanguan"] = dataGet[x].data[7].value
                linshi["faguzhang"] = dataGet[x].data[8].value
                linshi["2ya"] = dataGet[x].data[9].value
                // linshi["2yak"] = dataGet[x].data[9].key
                // linshi["2yau"] = dataGet[x].data[9].unit

                linshi["2liu"] = dataGet[x].data[10].value
                linshi["1du"] = dataGet[x].data[11].value
                linshi["2du"] = dataGet[x].data[12].value
                linshi["3du"] = dataGet[x].data[13].value

                if (formInline.deviceName == "01e844f884844aa2bb5d1cab87316c17") {
                    linshi["name"] = "焙烧湿电"
                    linshi["4du"] = dataGet[x].data[14].value
                    console.log(linshi);
                } else if (formInline.deviceName == "5cba298477bc456ab1a2bd06e35cb0d8") {
                    linshi["name"] = "隧道窑湿电"
                    console.log(linshi);
                }
                else {
                    linshi["name"] = "石墨化湿电"
                    console.log(linshi);
                }
                vocDate.vocDate1.push(linshi)
                console.log('21212121', vocDate.vocDate1);
            }
        }
        vocDate.vocDate1.splice(1, 0)
    });
}
